'use client';

import React, { useEffect, useRef, useState } from 'react';

const GOOGLE_MAPS_API_KEY = process.env.NEXT_PUBLIC_GOOGLE_MAPS_API_KEY || '';

interface Property {
  id: string;
  address: string;
  price: string;
  beds: number;
  baths: number;
  sqft: string;
  type: string;
  status: 'For Sale' | 'For Rent' | 'Sold';
  position: { lat: number; lng: number };
}

export default function RedfinStyleMap() {
  const mapRef = useRef<HTMLDivElement>(null);
  const mapInstanceRef = useRef<google.maps.Map | null>(null);
  const markersRef = useRef<google.maps.Marker[]>([]);
  const [isLoaded, setIsLoaded] = useState(false);
  const [selectedProperty, setSelectedProperty] = useState<Property | null>(null);
  const [isAddingProperty, setIsAddingProperty] = useState(false);
  const [properties, setProperties] = useState<Property[]>([
    {
      id: '1',
      address: '123 Forest Hills Blvd, Queens, NY',
      price: '$850,000',
      beds: 3,
      baths: 2,
      sqft: '1,800',
      type: 'Single Family',
      status: 'For Sale',
      position: { lat: 40.7200, lng: -73.8440 }
    },
    {
      id: '2',
      address: '456 Austin Street, Queens, NY',
      price: '$2,500/mo',
      beds: 2,
      baths: 1,
      sqft: '1,200',
      type: 'Apartment',
      status: 'For Rent',
      position: { lat: 40.7207, lng: -73.8441 }
    },
    {
      id: '3',
      address: '789 Queens Boulevard, Queens, NY',
      price: '$1,200,000',
      beds: 4,
      baths: 3,
      sqft: '2,400',
      type: 'Condo',
      status: 'For Sale',
      position: { lat: 40.7213, lng: -73.8458 }
    }
  ]);

  // Load Google Maps
  useEffect(() => {
    if ((window as any).google?.maps) {
      setIsLoaded(true);
      return;
    }

    const script = document.createElement('script');
    script.src = `https://maps.googleapis.com/maps/api/js?key=${GOOGLE_MAPS_API_KEY}&v=weekly`;
    script.async = true;
    script.onload = () => setIsLoaded(true);
    document.head.appendChild(script);

    return () => {
      if (script.parentNode) {
        document.head.removeChild(script);
      }
    };
  }, []);

  // Initialize map
  useEffect(() => {
    if (!isLoaded || !mapRef.current || mapInstanceRef.current) return;

    const google = (window as any).google;
    const map = new google.maps.Map(mapRef.current, {
      zoom: 15,
      center: { lat: 40.7200, lng: -73.8440 },
      mapTypeControl: true,
      streetViewControl: true,
      fullscreenControl: true,
    });

    // Add click listener to add new properties
    map.addListener('click', (e: any) => {
      if (!isAddingProperty) return;
      
      const lat = e.latLng.lat();
      const lng = e.latLng.lng();
      
      // Open form to add property details
      const address = prompt('Enter property address:');
      const price = prompt('Enter price (e.g., $500,000 or $2,000/mo):');
      const beds = prompt('Number of bedrooms:');
      const baths = prompt('Number of bathrooms:');
      const sqft = prompt('Square footage:');
      const type = prompt('Property type (e.g., House, Condo, Apartment):');
      
      if (address && price && beds && baths && sqft && type) {
        const newProperty: Property = {
          id: Date.now().toString(),
          address,
          price,
          beds: parseInt(beds),
          baths: parseFloat(baths),
          sqft,
          type,
          status: price.includes('/mo') ? 'For Rent' : 'For Sale',
          position: { lat, lng }
        };
        
        setProperties(prev => [...prev, newProperty]);
      }
    });

    mapInstanceRef.current = map;
  }, [isLoaded, isAddingProperty]);

  // Add markers for properties
  useEffect(() => {
    if (!isLoaded || !mapInstanceRef.current) return;

    const google = (window as any).google;
    const map = mapInstanceRef.current;

    // Clear existing markers
    markersRef.current.forEach(marker => marker.setMap(null));
    markersRef.current = [];

    // Add marker for each property
    properties.forEach(property => {
      const markerLabel = property.price.replace(/,/g, '');
      
      // Create custom marker with price label
      const marker = new google.maps.Marker({
        position: property.position,
        map: map,
        title: property.address,
        label: {
          text: property.price,
          color: 'white',
          fontSize: '12px',
          fontWeight: 'bold'
        },
        icon: {
          path: google.maps.SymbolPath.CIRCLE,
          scale: 25,
          fillColor: property.status === 'For Sale' ? '#DC2626' : 
                     property.status === 'For Rent' ? '#2563EB' : '#6B7280',
          fillOpacity: 1,
          strokeColor: 'white',
          strokeWeight: 2,
        }
      });

      // Info window
      const infoWindow = new google.maps.InfoWindow({
        content: createInfoWindowContent(property)
      });

      marker.addListener('click', () => {
        infoWindow.open(map, marker);
        setSelectedProperty(property);
        map.panTo(property.position);
      });

      markersRef.current.push(marker);
    });
  }, [isLoaded, properties]);

  const createInfoWindowContent = (property: Property) => {
    return `
      <div style="padding: 12px; min-width: 250px; font-family: Arial, sans-serif;">
        <div style="display: flex; justify-content: space-between; align-items: start; margin-bottom: 8px;">
          <h3 style="margin: 0; font-size: 18px; font-weight: bold; color: #1f2937;">${property.price}</h3>
          <span style="background: ${property.status === 'For Sale' ? '#DC2626' : property.status === 'For Rent' ? '#2563EB' : '#6B7280'}; 
                       color: white; padding: 2px 8px; border-radius: 4px; font-size: 11px; font-weight: bold;">
            ${property.status}
          </span>
        </div>
        <p style="margin: 4px 0; color: #6b7280; font-size: 13px;">${property.address}</p>
        <div style="display: flex; gap: 12px; margin: 8px 0; font-size: 14px; color: #374151;">
          <span><strong>${property.beds}</strong> beds</span>
          <span><strong>${property.baths}</strong> baths</span>
          <span><strong>${property.sqft}</strong> sqft</span>
        </div>
        <p style="margin: 4px 0; font-size: 12px; color: #6b7280;">${property.type}</p>
        <button 
          onclick="window.deleteProperty('${property.id}')"
          style="margin-top: 8px; width: 100%; background-color: #EF4444; color: white; padding: 6px; 
                 border: none; border-radius: 4px; cursor: pointer; font-size: 13px; font-weight: 500;">
          Remove Property
        </button>
      </div>
    `;
  };

  // Delete property function
  useEffect(() => {
    (window as any).deleteProperty = (propertyId: string) => {
      setProperties(prev => prev.filter(p => p.id !== propertyId));
      setSelectedProperty(null);
    };

    return () => {
      delete (window as any).deleteProperty;
    };
  }, []);

  const filterByStatus = (status: Property['status']) => {
    const filtered = properties.filter(p => p.status === status);
    if (filtered.length > 0 && mapInstanceRef.current) {
      const bounds = new (window as any).google.maps.LatLngBounds();
      filtered.forEach(p => bounds.extend(p.position));
      mapInstanceRef.current.fitBounds(bounds);
    }
  };

  return (
    <div className="h-screen w-full relative">
      {/* Top Control Bar */}
      <div className="absolute top-4 left-4 right-4 z-10 flex gap-4">
        {/* Left Controls */}
        <div className="bg-white rounded-lg shadow-lg p-3 flex gap-2">
          <button
            onClick={() => setIsAddingProperty(!isAddingProperty)}
            className={`px-4 py-2 rounded-lg font-semibold transition ${
              isAddingProperty 
                ? 'bg-red-500 text-white hover:bg-red-600' 
                : 'bg-blue-500 text-white hover:bg-blue-600'
            }`}
          >
            {isAddingProperty ? '‚ùå Cancel' : '‚ûï Add Property'}
          </button>

          <button
            onClick={() => filterByStatus('For Sale')}
            className="px-4 py-2 bg-red-600 text-white rounded-lg font-semibold hover:bg-red-700 transition"
          >
            For Sale ({properties.filter(p => p.status === 'For Sale').length})
          </button>

          <button
            onClick={() => filterByStatus('For Rent')}
            className="px-4 py-2 bg-blue-600 text-white rounded-lg font-semibold hover:bg-blue-700 transition"
          >
            For Rent ({properties.filter(p => p.status === 'For Rent').length})
          </button>
        </div>

        {/* Status Indicator */}
        {isAddingProperty && (
          <div className="bg-yellow-100 border-2 border-yellow-400 rounded-lg px-4 py-2 font-semibold text-yellow-800">
            üìç Click on map to add a property
          </div>
        )}
      </div>

      {/* Property Details Sidebar */}
      {selectedProperty && (
        <div className="absolute top-4 right-4 z-10 bg-white rounded-lg shadow-xl p-4 w-80 max-h-96 overflow-y-auto">
          <button
            onClick={() => setSelectedProperty(null)}
            className="absolute top-2 right-2 text-gray-500 hover:text-gray-700 text-xl"
          >
            ‚úï
          </button>
          
          <div className="mb-3">
            <span className={`inline-block px-3 py-1 rounded-full text-xs font-bold text-white ${
              selectedProperty.status === 'For Sale' ? 'bg-red-600' :
              selectedProperty.status === 'For Rent' ? 'bg-blue-600' : 'bg-gray-600'
            }`}>
              {selectedProperty.status}
            </span>
          </div>

          <h2 className="text-2xl font-bold text-gray-900 mb-2">{selectedProperty.price}</h2>
          <p className="text-gray-600 mb-4">{selectedProperty.address}</p>

          <div className="grid grid-cols-3 gap-4 mb-4 text-center">
            <div>
              <div className="text-2xl font-bold text-gray-900">{selectedProperty.beds}</div>
              <div className="text-xs text-gray-600">Bedrooms</div>
            </div>
            <div>
              <div className="text-2xl font-bold text-gray-900">{selectedProperty.baths}</div>
              <div className="text-xs text-gray-600">Bathrooms</div>
            </div>
            <div>
              <div className="text-2xl font-bold text-gray-900">{selectedProperty.sqft}</div>
              <div className="text-xs text-gray-600">Sq Ft</div>
            </div>
          </div>

          <div className="border-t pt-3">
            <p className="text-sm text-gray-600 mb-1">
              <strong>Type:</strong> {selectedProperty.type}
            </p>
            <p className="text-sm text-gray-600">
              <strong>Location:</strong> {selectedProperty.position.lat.toFixed(4)}, {selectedProperty.position.lng.toFixed(4)}
            </p>
          </div>
        </div>
      )}

      {/* Properties List */}
      <div className="absolute bottom-4 left-4 z-10 bg-white rounded-lg shadow-lg p-4 max-w-sm max-h-64 overflow-y-auto">
        <h3 className="font-bold text-lg mb-3">All Properties ({properties.length})</h3>
        <div className="space-y-2">
          {properties.map(property => (
            <button
              key={property.id}
              onClick={() => {
                setSelectedProperty(property);
                if (mapInstanceRef.current) {
                  mapInstanceRef.current.panTo(property.position);
                  mapInstanceRef.current.setZoom(17);
                }
              }}
              className={`w-full text-left p-2 rounded transition ${
                selectedProperty?.id === property.id
                  ? 'bg-blue-100 border-2 border-blue-500'
                  : 'bg-gray-50 hover:bg-gray-100'
              }`}
            >
              <div className="font-semibold text-sm">{property.price}</div>
              <div className="text-xs text-gray-600">{property.beds}bd ‚Ä¢ {property.baths}ba ‚Ä¢ {property.sqft} sqft</div>
            </button>
          ))}
        </div>
      </div>

      {/* Map */}
      <div ref={mapRef} className={`h-full w-full ${isAddingProperty ? 'cursor-crosshair' : ''}`} />
      
      {!isLoaded && (
        <div className="absolute inset-0 flex items-center justify-center bg-gray-100">
          <p className="text-gray-600">Loading Map...</p>
        </div>
      )}
    </div>
  );
}